//  Init Nimda
@import "_init";


/* CHANGELISTS */

#changelist {
    position: relative;
    width: 100%;
}

#changelist table {
    width: 100%;
}

#changelist-search input[type="submit"] {
    @include button($color-hero);
    padding: 8px $distance;
    margin: 0 5px;
}

.change-list .hiddenfields { display:none; }

.change-list .filtered table {
    border-right: 1px solid #ddd;
}

.change-list .filtered {
    min-height: 400px;
}

.change-list .object-tools.filter {
    margin-right: 160px;
}

.change-list .filtered {
    background: white url(../img/changelist-bg.gif) top right repeat-y !important;
}

.change-list .filtered .results, .change-list .filtered .paginator, .filtered #toolbar, .filtered div.xfull {
    margin-right: 160px !important;
    width: auto !important;
}

.change-list .filtered table tbody th {
    padding-right: 1em;
}

#changelist-form .results {
  overflow-x: auto;
  border-top: 1px solid $color-grey-light;

  img[alt="True"], img[alt="False"] {
    border-radius: 50%;
    overflow: hidden;
  }
}

#changelist .toplinks {
    border-bottom: 1px solid #ccc !important;
}

#changelist .paginator {
    color: $color-grey;
    // border-top: 1px solid #eee;
    overflow: hidden;
}

.change-list .filtered .paginator {
    border-right: 1px solid #ddd;
}

/* CHANGELIST TABLES */

#changelist table thead th {
    padding: 0;
    white-space: nowrap;
    vertical-align: middle;
}

#changelist table thead th.action-checkbox-column {
    width: 1.5em;
    text-align: center;
}

#changelist table tbody td, #changelist table tbody th {
    border-left: 1px solid #ddd;
}

#changelist table tbody td:first-child, #changelist table tbody th:first-child {
    border-left: 0;
    border-right: 1px solid #ddd;
}

#changelist table tbody td.action-checkbox {
    text-align:center;
}

#changelist table tfoot {
    color: #666;
}

/* TOOLBAR */

#changelist #toolbar {
    padding: $distance-s;
    // color: #666;
}

#changelist #toolbar form input {
    // font-size: 11px;
    // padding: 1px 2px;
}

#changelist #toolbar form #searchbar {

}

#changelist #changelist-search img {
    // vertical-align: middle;
    display: none;
}

/* FILTER COLUMN */

#changelist-filter {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1000;
    width: 160px;
    // background: #efefef;
    margin: 0;
}

#changelist-filter h2 {
    font-size: fs('m');
    padding: 2px 10px;
    color: #333;
    border-bottom: 1px solid #ddd;
}

#changelist-filter h3 {
    font-size: fs('s');
    margin-bottom: 0;
}

#changelist-filter ul {
    font-size: fs('s');
    padding-left: 0;
    margin-left: 10px;
}

#changelist-filter li {
    list-style-type: none;
    margin-left: 0;
    padding-left: 0;
}

#changelist-filter a {
    color: $color-grey-dark;
}

#changelist-filter li.selected {
    // border-left: 5px solid #ccc;
    padding-left: 10px;
    margin-left: -10px;
}

#changelist-filter li.selected a {
    color: $color-hero;
}

/* DATE DRILLDOWN */

.change-list ul.toplinks {
    display: block;
    background: white url(../img/nav-bg-reverse.gif) 0 -10px repeat-x;
    border-top: 1px solid white;
    float: left;
    padding: 0 !important;
    margin: 0 !important;
    width: 100%;
}

.change-list ul.toplinks li {
    padding: 3px 6px;
    font-weight: bold;
    list-style-type: none;
    display: inline-block;
}

.change-list ul.toplinks .date-back a {
    color: #999;
}

.change-list ul.toplinks .date-back a:hover {
    color: #036;
}

/* PAGINATOR */

.paginator {
    padding-top: 10px;
    padding-bottom: 10px;
    line-height: 22px;
    margin: 0;
    border-top: 1px solid #ddd;
}

.paginator a:link, .paginator a:visited {
    padding: 2px 6px;
    border: solid 1px #ccc;
    background: white;
    text-decoration: none;
}

.paginator a.showall {
    padding: 0 !important;
    border: none !important;
}

.paginator a.showall:hover {
    color: #036 !important;
    background: transparent !important;
}

.paginator .end {
    border-width: 2px !important;
    margin-right: 6px;
}

.paginator .this-page {
    padding: 2px 6px;
    font-weight: bold;
    font-size: 13px;
    vertical-align: top;
}

.paginator a:hover {
    color: white;
    background: #5b80b2;
    border-color: #036;
}

/* ACTIONS */

.filtered .actions {
    margin-right: 160px !important;
    // border-right: 1px solid #ddd;
    color: $color-grey;
    margin-left: 0.5em;
}

#changelist table input {
    margin: 0;
}

#changelist table tbody tr.selected {
    background-color: rgba($color-hero, 0.05);
}

#changelist .actions {
    padding: $distance-s;
}

#changelist #toolbar ~ #changelist-form .actions {
    @include breakpoint('min-width', 1000px) {
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 3px;
    }
}

#changelist .actions.selected {
    background: #fffccf;
    border-top: 1px solid #fffee8;
    border-bottom: 1px solid #edecd6;
}

#changelist .actions span.all,
#changelist .actions span.action-counter,
#changelist .actions span.clear,
#changelist .actions span.question {
    // font-size: 11px;
    margin: 0 0.5em;
    display: none;
}

#changelist .actions:last-child {
    border-bottom: none;
}

#changelist .actions select {
    border: 1px solid #aaa;
    margin-left: 0.5em;
    padding: 1px 2px;
}

#changelist .actions label {
    // font-size: 11px;

}

#changelist #action-toggle {
    display: none;
}

#changelist .actions .button {
    // font-size: 11px;
    font-size: fs('s');
    padding: 6px 8px;
    margin-left: 5px;
    background-color: $color-hero;
}
